import React from 'react'

import {
  GMenuCategoryListItem,
  WareCategoryTypeEnum,
} from '../../../graph/types'
import { RM } from '../../../helpers/ramda'
import { WithSettings } from '../../settings/atoms/WithSettings'
import { MenuSuggestionPanel } from '../atoms/MenuSuggestionPanel'
import { QueryCategoryList } from './CategoryList.graphql'

interface IProps {
  categoryType: WareCategoryTypeEnum
  onAdd(recipe: TCategory): void
  addButtonText: string
  currentCategories: TCategory[]
}

type TCategory = GMenuCategoryListItem.Fragment

export const CategorySuggestionPanel: React.SFC<IProps> = ({
  categoryType,
  currentCategories,
  addButtonText,
  onAdd,
}) => (
  <WithSettings
    observe={true}
    render={settings => (
      <QueryCategoryList
        {...settings.getQueryProps({ categoryType })}
        fetchPolicy="cache-and-network"
        render={({ wareCategories }) => (
          <MenuSuggestionPanel<TCategory>
            onAdd={onAdd}
            options={RM.without(currentCategories, wareCategories)}
            addButtonText={addButtonText}
          />
        )}
      />
    )}
  />
)
