interface IFormValue {
  pictogram: string
}

export const MenuCategoryPictogramSelect: React.SFC<IProps> = ({ field }) => (
  <Field field={field} pure={true}>
    {fieldApi => (
      <SelectControl<IFormValue>
        value={fieldApi.value ? { pictogram: fieldApi.value } : null}
        onChange={value => fieldApi.setValue(value)}
        options={availablePictograms}
        getOptionValue={RM.prop('pictogram')}
        // @ts-ignore Badly typed
        formatOptionLabel={(pictogram: string) => (
          <PictogramDisplay pictogramId={pictogram} fontSize={2} />
        )}
        components={{
          SingleValue: ({ data }) => (
            <PictogramDisplay pictogramId={data.pictogram} fontSize={4} />
          ),
        }}
        styles={{
          container: base => ({ ...base, width: '8rem' }),
          menuList: base => ({ ...base, maxHeight: '35rem' }),
        }}
        isClearable={false}
        isSearchable={false}
        minWidth={8}
      />
    )}
  </Field>
)
