import { i18nMark } from '@lingui/react'
import React from 'react'

import { FormInputFlatSelect } from '../../../atoms/FormInputFlatSelect'
import { IconLine } from '../../../atoms/IconLine'
import { FieldStateEnum } from '../../../graph/types'
import { i18n } from '../../../i18n'

interface IProps {}

const items = [
  {
    type: FieldStateEnum.DEFAULT,
    label: i18nMark('Not set'),
  },
  {
    type: FieldStateEnum.ENABLED,
    label: i18nMark('Permit'),
  },
  {
    type: FieldStateEnum.DISABLED,
    label: i18nMark('Forbid'),
  },
]

export const FormInputAllowNegative: React.SFC<IProps> = props => (
  <IconLine label={i18n.t`Negative stock`} labelWidth={7}>
    <FormInputFlatSelect
      field="allowNegative"
      items={items}
      formatItem={item => i18n._(item.label)}
      getItemId={item => item.type}
    />
  </IconLine>
)
