import React from 'react'

import { GMenuStockDeposit } from '../../../graph/types'
import { WithSettings } from '../../settings/atoms/WithSettings'
import {
  ChangeMenuStockQuantity,
  QueryMenuStockDeposit,
} from './StockDepositLoader.graphql'

export type TStockItem = GMenuStockDeposit.StockItems

export type TDepositFn = (stockItem: TStockItem, quantity: number) => void

interface IProps {
  children(stockItems: TStockItem[], onDeposit: TDepositFn): ReactNode
}

export const StockDepositLoader: React.SFC<IProps> = ({ children }) => (
  <ChangeMenuStockQuantity
    render={execute => {
      const onDeposit: TDepositFn = (stockItem, quantity) => {
        execute({
          variables: { stockItem: stockItem.id, quantityChange: quantity },
        })
      }
      return (
        <WithSettings
          render={settings => (
            <QueryMenuStockDeposit
              {...settings.queryProps}
              render={({ stockItems }) => children(stockItems, onDeposit)}
            />
          )}
        />
      )
    }}
  />
)
