import { Trans } from '@lingui/react'
import React from 'react'

import {
  Column,
  ColumnRendererEdit,
  DataTable,
  NoteText,
} from '../../../atoms/DataTable'
import { Headline } from '../../../atoms/Headline'
import { i18n } from '../../../i18n'
import { QueryStockRecipes } from './StockRecipesView.graphql'

interface IProps {
  stockId: Maybe<ID>
}

export const StockRecipesView: React.SFC<IProps> = ({ stockId }) =>
  stockId === null ? null : (
    <QueryStockRecipes
      variables={{ stockId }}
      render={({ stockItem: { ingredients } }) =>
        ingredients.length > 0 ? (
          <>
            <Headline>
              <Trans>Recipes using this card</Trans>
            </Headline>

            <DataTable items={ingredients}>
              <Column
                dataKey=""
                cellDataGetter={({ rowData }) =>
                  `../../recipe/${rowData.recipe.id}`
                }
                cellRenderer={ColumnRendererEdit}
                width={100}
              />
              <Column
                dataKey="recipe"
                width={300}
                label={i18n.t`Name`}
                cellRenderer={({ rowData }) => (
                  <>
                    {rowData.recipe.nameLabel}
                    <NoteText>{rowData.recipe.descriptionLabel}</NoteText>
                  </>
                )}
              />
            </DataTable>
          </>
        ) : null
      }
    />
  )
