import { DocumentNode } from 'graphql'
import React from 'react'
import { Mutation, MutationFn, MutationProps } from 'react-apollo'

export function buildMutation<TData, TVariables>(mutation: DocumentNode) {
  interface IProps
    extends Omit<MutationProps<TData, TVariables>, 'mutation' | 'children'> {
    render(result: MutationFn<TData, TVariables>): ReactNode
  }
  const GMutation: React.SFC<IProps> = ({ render, children, ...props }) => (
    <Mutation mutation={mutation} {...props}>
      {render}
    </Mutation>
  )
  return GMutation
}
