import React from 'react'

import { WithRoot } from '../../../atoms/WithRoot'
import { RecipeListTable } from '../molecules/RecipeListTable'
import { QueryRecipeList } from './RecipeList.graphql'

interface IProps {}

export const RecipeListLoader: React.SFC<IProps> = props => (
  <WithRoot
    render={({ user, settings }) => (
      <QueryRecipeList
        {...settings.queryProps}
        renderLoading={false}
        render={({ recipes }, { loading }) => (
          <RecipeListTable
            recipes={recipes}
            onToggle={settings.menu.recipe.changeVisibility}
            isLoading={loading}
            heightStretch={true}
            showTypeColumn={true}
          />
        )}
      />
    )}
  />
)
