import { Trans } from '@lingui/react'
import React from 'react'
import { TableCellProps } from 'react-virtualized'

import {
  checkColumnAccess,
  Column,
  ColumnRendererEdit,
  DataTable,
} from '../../../atoms/DataTable'
import { Loading } from '../../../atoms/Loading'
import { GMenuStockList } from '../../../graph/types'
import { i18n } from '../../../i18n'

type TStockItem = GMenuStockList.StockItems

interface IProps {
  stockItems: TStockItem[]
  isLoading: boolean
}

export const StockListTable: React.SFC<IProps> = ({
  stockItems,
  isLoading,
}) => (
  <DataTable
    items={stockItems}
    heightStretch={true}
    noRowsRenderer={() =>
      isLoading ? (
        <Loading />
      ) : (
        <h3>
          <Trans>Selected branch has no stock cards</Trans>
        </h3>
      )
    }
  >
    <Column
      dataKey="id"
      width={60}
      cellRenderer={checkColumnAccess(
        { canQuery: 'stockItem' },
        ColumnRendererEdit,
      )}
    />
    <Column dataKey="code" width={100} label={i18n.t`Card code`} />
    <Column dataKey="nameLabel" width={300} label={i18n.t`Card name`} />
    <Column
      dataKey="quantity"
      width={100}
      label={i18n.t`Card stock`}
      cellRenderer={stockWithUnit}
    />
    <Column dataKey="note" width={300} label={i18n.t`Card note`} />
  </DataTable>
)

function stockWithUnit({ cellData, rowData }: TableCellProps) {
  return `${cellData} ${rowData.wareUnit.code}`
}
