import gql from 'graphql-tag'

import { buildMutation } from '../../../graph/buildMutation'
import { buildQuery } from '../../../graph/buildQuery'
import {
  GChangeMenuStockQuantity,
  GMenuStockDeposit,
} from '../../../graph/types'
import { WareUnitFragment } from '../graph/WareUnitFragment.graphql'

const MenuStockDepositQuery = gql`
  query GMenuStockDeposit($branchId: ID!) {
    stockItems(companyBranch: $branchId) {
      id
      code
      plu
      nameLabel: label
      note
      quantity
      wareUnit {
        ...GWareUnit
      }
    }
  }
  ${WareUnitFragment}
`

const ChangeMenuStockQuantityMutation = gql`
  mutation GChangeMenuStockQuantity($stockItem: ID!, $quantityChange: Float!) {
    changeAmountStockItem(id: $stockItem, amountOffset: $quantityChange) {
      id
      quantity
    }
  }
`

export const ChangeMenuStockQuantity = buildMutation<
  GChangeMenuStockQuantity.Mutation,
  GChangeMenuStockQuantity.Variables
>(ChangeMenuStockQuantityMutation)

export const QueryMenuStockDeposit = buildQuery<
  GMenuStockDeposit.Query,
  GMenuStockDeposit.Variables
>(MenuStockDepositQuery)
