import React from 'react'

import {
  checkColumnAccess,
  Column,
  ColumnRendererEdit,
  ColumnRendererRemove,
  ColumnRendererVisibility,
  ColumnRendererWithNote,
  DataTable,
  DataTableEmptyText,
  IDataTableProps,
} from '../../../atoms/DataTable'
import { DragHandle } from '../../../atoms/DragHandle'
import { GMenuCategoryListItem } from '../../../graph/types'
import { i18n } from '../../../i18n'
import {
  SortableDataTable,
  TSortHandler,
} from '../../../molecules/SortableDataTable'
import { getCategoryRoute } from '../models/MenuCategoryModel'

type TCategory = GMenuCategoryListItem.Fragment

interface IProps {
  categories: TCategory[]
  isSortable?: boolean
  isLoading?: boolean
  showEditColumn?: boolean
  showRemoveColumn?: boolean
  showVisibilityColumn?: boolean
  showHeader?: boolean
  onToggle?(categoryId: ID, visible: boolean): void
  onRemove?(categoryId: ID): void
  onSort?: TSortHandler<TCategory>
  heightStretch?: boolean
  noRowsDisplay?: ReactNode
}

export const CategoryListTable: React.SFC<IProps> = ({
  categories,
  onToggle,
  onRemove,
  onSort,
  showEditColumn = true,
  showRemoveColumn = false,
  showVisibilityColumn = false,
  showHeader = true,
  isSortable = false,
  isLoading = false,
  heightStretch = false,
  noRowsDisplay = () => (
    <DataTableEmptyText text={i18n.t`No recipes in category`} />
  ),
}) => {
  const props: IDataTableProps<TCategory> = {
    isLoading,
    items: categories,
    disableHeader: !showHeader,
    noRowsRenderer: () => noRowsDisplay,
  }
  let Table: React.SFC = ({ children }) =>
    isSortable ? (
      <SortableDataTable
        heightStretch={heightStretch}
        onSort={onSort}
        {...props}
      >
        {children}
      </SortableDataTable>
    ) : (
      <DataTable heightStretch={heightStretch} {...props}>
        {children}
      </DataTable>
    )
  return (
    <Table>
      {showEditColumn && (
        <Column
          dataKey="id"
          width={60}
          cellRenderer={checkColumnAccess(
            { canQuery: 'wareCategory' },
            ColumnRendererEdit,
          )}
          cellDataGetter={({ rowData }) =>
            getCategoryRoute(rowData.id, rowData.categoryType.enum)
          }
        />
      )}
      {showRemoveColumn && (
        <Column
          dataKey="id"
          width={60}
          cellRenderer={ColumnRendererRemove}
          columnData={{ onRemove }}
        />
      )}
      {showVisibilityColumn && (
        <Column
          dataKey="visible"
          width={60}
          cellRenderer={checkColumnAccess(
            { canMutate: 'updateWareCategory' },
            ColumnRendererVisibility,
          )}
          columnData={{ onToggle }}
        />
      )}
      <Column
        dataKey="nameLabel"
        width={300}
        label={i18n.t`Name`}
        cellRenderer={ColumnRendererWithNote}
        columnData={{ noteKey: 'internalNoteLabel' }}
      />
      <Column
        dataKey="countRecipes"
        label={i18n.t`Recipe count`}
        width={100}
        cellRenderer={({ cellData }) =>
          `${cellData} ${i18n.t`Recipes`.toLocaleLowerCase()}`
        }
      />
      {isSortable && (
        <Column dataKey="id" width={50} cellRenderer={() => <DragHandle />} />
      )}
    </Table>
  )
}
