import React from 'react'

import { FlexColumn } from '../../../atoms/FlexColumn'
import { FlexRowSingle } from '../../../atoms/FlexRowSingle'
import { Headline } from '../../../atoms/Headline'
import { WithRoot } from '../../../atoms/WithRoot'
import { WareCategoryTypeEnum } from '../../../graph/types'
import { getCategoryRoute } from '../models/MenuCategoryModel'
import { CategoryListTable } from '../molecules/CategoryListTable'
import { CreateButton } from '../molecules/CreateButton'
import { QueryCategoryList } from './CategoryList.graphql'

interface ICategoryListProps {
  headline: string
  subHeadline?: string
  categoryType: WareCategoryTypeEnum
  heightStretch?: boolean
}

export const CategoryList: React.SFC<ICategoryListProps> = ({
  headline,
  subHeadline = false,
  categoryType,
  heightStretch = false,
}) => (
  <>
    <FlexRowSingle>
      <FlexColumn widthGrowIndex={1}>
        <Headline>{headline}</Headline>
        {subHeadline && <Headline size="smaller">{subHeadline}</Headline>}
      </FlexColumn>
      <FlexColumn fixedWidth={true}>
        <CreateButton routeTo={getCategoryRoute('create', categoryType)} />
      </FlexColumn>
    </FlexRowSingle>
    <WithRoot
      render={({ settings }) => (
        <QueryCategoryList
          {...settings.getQueryProps({ categoryType })}
          renderLoading={true}
          render={({ wareCategories }) => (
            <CategoryListTable
              categories={wareCategories}
              onToggle={settings.menu.category.changeVisibility}
              isSortable={true}
              showHeader={false}
              heightStretch={heightStretch}
              showVisibilityColumn={true}
              onSort={({ item, offset }) => {
                settings.menu.category.sortCategory(item.id, offset)
              }}
            />
          )}
        />
      )}
    />
  </>
)
