import React from 'react'

import { WithRoot } from '../../../atoms/WithRoot'
import { QueryMenuStockList } from './StockList.graphql'
import { StockListTable } from './StockListTable'

export const StockListLoader: React.SFC = () => (
  <WithRoot
    observe={true}
    render={({ settings }) => (
      <QueryMenuStockList
        {...settings.queryProps}
        fetchPolicy="cache-and-network"
        renderLoading={false}
        render={({ stockItems }, { loading }) => (
          <StockListTable stockItems={stockItems} isLoading={loading} />
        )}
      />
    )}
  />
)
