import React from 'react'
import { TableCellProps } from 'react-virtualized'

import {
  checkColumnAccess,
  Column,
  ColumnRendererEdit,
  ColumnRendererRemove,
  ColumnRendererVisibility,
  ColumnRendererWithNote,
  DataTable,
} from '../../../atoms/DataTable'
import { DragHandle } from '../../../atoms/DragHandle'
import { GMenuRecipeList, WareCategoryTypeEnum } from '../../../graph/types'
import { RM } from '../../../helpers/ramda'
import { i18n } from '../../../i18n'
import {
  SortableDataTable,
  TSortHandler,
} from '../../../molecules/SortableDataTable'
import { TPriceType } from '../menutypes'
import { getRecipeRoute } from '../models/MenuRecipeModel'

type TRecipe = GMenuRecipeList.Recipes

interface IProps {
  recipes: TRecipe[]
  isSortable?: boolean
  isLoading?: boolean
  showEditColumn?: boolean
  showRemoveColumn?: boolean
  showVisibilityColumn?: boolean
  showTypeColumn?: boolean
  onToggle?(recipeId: ID, visible: boolean): void
  onRemove?(recipeId: ID): void
  onSortRecipe?: TSortHandler<TRecipe>
  heightStretch?: boolean
}

export const RecipeListTable: React.SFC<IProps> = ({
  recipes,
  onToggle,
  onRemove,
  onSortRecipe,
  showEditColumn = true,
  showRemoveColumn = false,
  showVisibilityColumn = true,
  showTypeColumn = false,
  isSortable = false,
  isLoading = false,
  heightStretch = false,
}) => {
  const props = {
    isLoading,
    items: recipes,
  }
  let Table: React.SFC = ({ children }) =>
    isSortable ? (
      <SortableDataTable
        heightStretch={heightStretch}
        onSort={onSortRecipe}
        {...props}
      >
        {children}
      </SortableDataTable>
    ) : (
      <DataTable heightStretch={heightStretch} {...props}>
        {children}
      </DataTable>
    )
  return (
    <Table>
      {showEditColumn && (
        <Column
          dataKey="id"
          width={60}
          cellRenderer={checkColumnAccess(
            { canQuery: 'recipe' },
            ColumnRendererEdit,
          )}
          cellDataGetter={({ rowData }) =>
            getRecipeRoute(rowData.id, rowData.recipeType.enum)
          }
        />
      )}
      {showRemoveColumn && (
        <Column
          dataKey="id"
          width={60}
          cellRenderer={ColumnRendererRemove}
          columnData={{ onRemove }}
        />
      )}
      {showVisibilityColumn && (
        <Column
          dataKey="visible"
          width={60}
          cellRenderer={checkColumnAccess(
            { canMutate: 'updateRecipe' },
            ColumnRendererVisibility,
          )}
          columnData={{ onToggle }}
        />
      )}
      {showTypeColumn && (
        <Column
          dataKey="recipeType"
          width={100}
          label={i18n.t`Type`}
          cellRenderer={ColumnRendererCode}
        />
      )}
      <Column dataKey="code" width={100} label={i18n.t`Code`} />
      <Column
        dataKey="nameLabel"
        width={300}
        label={i18n.t`Name`}
        cellRenderer={ColumnRendererWithNote}
        columnData={{ noteKey: 'internalNote' }}
      />
      <Column
        dataKey="prices"
        width={150}
        label={i18n.t`Delivery sale`}
        cellRenderer={ColumnRendererPrice}
        columnData={{ priceType: TPriceType.DELIVERY }}
      />
      <Column
        dataKey="prices"
        width={150}
        label={i18n.t`Local sale`}
        cellRenderer={ColumnRendererPrice}
        columnData={{ priceType: TPriceType.INHOUSE }}
      />
      <Column
        dataKey="availableAmount"
        label={i18n.t('recipe.list.availableAmount')`Stock`}
        width={100}
      />
      {isSortable && (
        <Column dataKey="id" width={50} cellRenderer={() => <DragHandle />} />
      )}
    </Table>
  )
}

function ColumnRendererPrice({
  cellData: prices,
  columnData: { priceType },
}: TableCellProps) {
  const foundPrice = prices.find(RM.pathEq(['priceType', 'enum'], priceType))
  if (foundPrice) {
    return foundPrice.price.formattedValue
  }
  return <i>{i18n.t`Not set`.toLocaleLowerCase()}</i>
}

function ColumnRendererCode({ rowData }: TableCellProps) {
  const type: WareCategoryTypeEnum = rowData.recipeType.enum
  if (type === WareCategoryTypeEnum.SIDE_DISH) {
    return i18n.t`Sidedish`.toLocaleLowerCase()
  }
  if (type === WareCategoryTypeEnum.COVER) {
    return i18n.t`Cover`.toLocaleLowerCase()
  }
  return i18n.t`Ware`.toLocaleLowerCase()
}
