import React from 'react'
import { isDecimal, isEmpty } from 'validator'

import {
  FormInputTextBox,
  TFormInputTextBoxProps,
} from '../../../atoms/FormInputTextBox'
import { IconLine } from '../../../atoms/IconLine'
import { i18n } from '../../../i18n'

interface IProps extends Partial<TFormInputTextBoxProps> {
  icon?: string
}

const formInputPriceValidate = (value: string) => {
  if (value === undefined || isEmpty(value + '') || !isDecimal(value + '')) {
    return i18n.t`Price must be a number`
  }
  return null
}

export const FormInputPrice: React.SFC<IProps> = ({
  field = 'price.value',
  icon = 'toll',
  ...props
}) => (
  <IconLine icon={icon}>
    <FormInputTextBox
      field={field}
      inputWidth={5}
      validate={formInputPriceValidate}
      {...props}
    />
    Kč
  </IconLine>
)
