import React from 'react'

import { WareCategoryTypeEnum } from '../../../graph/types'

interface IProps {
  type: WareCategoryTypeEnum
  isMain?: boolean
  isSideDish?: boolean
  isCover?: boolean
  children: ReactNode | ReactNode[]
}

export const IsCategoryType: React.SFC<IProps> = ({
  type,
  isMain,
  isSideDish,
  isCover,
  children,
}) => {
  if (
    (isMain === true && type === WareCategoryTypeEnum.MAIN) ||
    (isSideDish === true && type === WareCategoryTypeEnum.SIDE_DISH) ||
    (isCover === true && type === WareCategoryTypeEnum.COVER)
  ) {
    return <>{children}</>
  }
  return null
}
