import 'react-virtualized/styles.css'

import React, { Component } from 'react'
import {
  AutoSizer,
  Column,
  Table,
  TableCellProps,
  TableCellRenderer,
  TableProps,
  WindowScroller,
} from 'react-virtualized'
import styled from 'styled-components'

import { i18n } from '../i18n'
import { TouchButtonEdit } from '../molecules/TouchButtonEdit'
import { TouchButtonRemove } from '../molecules/TouchButtonRemove'
import { TouchButtonVisibility } from '../molecules/TouchButtonVisibility'
import { Colors } from '../styling'
import { CanMutate } from './CanMutate'
import { CanQuery } from './CanQuery'
import Pacman from './Pacman'

export interface IDataTableProps<T> extends Partial<TableProps> {
  items: T[]
  isLoading?: boolean
  heightStretch?: boolean
}

export class DataTable<T> extends Component<IDataTableProps<T>> {
  static defaultProps = {
    heightStretch: false,
  }
  renderTable(width: number, height: number) {
    const { items, children, isLoading, heightStretch, ...props } = this.props

    return (
      <Table
        autoHeight={!heightStretch}
        width={width}
        height={height}
        headerHeight={50}
        rowHeight={60}
        rowCount={items ? items.length : 0}
        rowGetter={({ index }) => items[index]}
        noRowsRenderer={() => (isLoading ? <Pacman /> : null)}
        {...props}
      >
        {children}
      </Table>
    )
  }
  render() {
    return this.props.heightStretch ? (
      <AutoSizer>
        {({ width, height }) => this.renderTable(width, height)}
      </AutoSizer>
    ) : (
      <WindowScroller scrollElement={window}>
        {({ height }) => (
          <AutoSizer disableHeight={true}>
            {({ width }) => this.renderTable(width, height)}
          </AutoSizer>
        )}
      </WindowScroller>
    )
  }
}

export function checkColumnAccess(
  { canQuery, canMutate }: { canQuery?: string; canMutate?: string },
  renderer: TableCellRenderer,
) {
  return (cellProps: TableCellProps) => {
    let result = renderer(cellProps)
    if (canQuery) {
      result = <CanQuery queryName={canQuery}>{result}</CanQuery>
    }
    if (canMutate) {
      result = <CanMutate mutationName={canMutate}>{result}</CanMutate>
    }
    return result
  }
}

export function ColumnRendererEdit({ cellData }: TableCellProps) {
  return <TouchButtonEdit target={cellData} />
}

export function ColumnRendererRemove({ cellData, columnData }: TableCellProps) {
  return (
    <TouchButtonRemove
      onClick={() => columnData.onRemove && columnData.onRemove(cellData)}
    />
  )
}

export function ColumnRendererVisibility({
  cellData,
  columnData,
  rowData,
}: TableCellProps) {
  return (
    <TouchButtonVisibility
      isVisible={cellData}
      onClick={() => columnData.onToggle(rowData.id, !cellData)}
    />
  )
}

export const NoteText = styled.div`
  color: ${Colors.greyMiddle};
`

export function ColumnRendererWithNote({
  cellData,
  rowData,
  columnData,
}: TableCellProps) {
  return (
    <>
      {cellData}
      <NoteText>{rowData[columnData.noteKey]}</NoteText>
    </>
  )
}

export const DataTableEmptyText = styled(
  ({ text = i18n.t`Table is empty`, ...props }: { text?: string }) => (
    <div {...props}>{text}</div>
  ),
)`
  padding: 1rem;
  color: ${Colors.greyMiddle};
  font-style: 'italic';
`

export { Column }
