import React, { Component } from 'react'

import { FlexColumn } from '../../../atoms/FlexColumn'
import { FlexRowSingle } from '../../../atoms/FlexRowSingle'
import {
  ISelectControlProps,
  SelectControl,
} from '../../../atoms/SelectControl'
import { RM } from '../../../helpers/ramda'
import { i18n } from '../../../i18n'
import { TouchButton } from '../../../molecules/TouchButton'

interface IProps<OptionType> extends ISelectControlProps {
  onAdd(option: OptionType): void
  addLabel?: string
  addButtonText?: string
}

export class MenuSuggestionPanel<OptionType extends {}> extends Component<
  IProps<OptionType>
> {
  static defaultProps = {
    addButtonText: i18n.t`Add`,
  }
  state: { selected: Maybe<OptionType> } = { selected: null }
  onAdd = () => {
    if (this.state.selected) {
      this.props.onAdd(this.state.selected!)
      this.setState({ selected: null })
    }
  }
  onSelect = (selected: OptionType) => {
    this.setState({ selected })
  }
  render() {
    const {
      addLabel = i18n.t`Add`,
      onAdd,
      addButtonText,
      ...props
    } = this.props
    return (
      <FlexRowSingle justify="flex-start">
        <FlexColumn fixedWidth={true} width="4rem" margin="auto 0">
          {addLabel}
        </FlexColumn>
        <FlexColumn fixedWidth={true} width="20rem" margin="auto 0">
          <SelectControl
            value={this.state.selected}
            onChange={this.onSelect}
            onKeyDown={ev => {
              if (ev.key === 'Enter') {
                this.onAdd()
              }
            }}
            isClearable={false}
            getOptionValue={RM.prop('id')}
            getOptionLabel={RM.prop('nameLabel')}
            {...props}
          />
        </FlexColumn>
        <FlexColumn fixedWidth={true} width="10rem" margin="auto 0">
          <TouchButton
            text={addButtonText}
            disabled={this.state.selected === null}
            onClick={this.onAdd}
          />
        </FlexColumn>
      </FlexRowSingle>
    )
  }
}
